import { Component, EventEmitter, Prop, Event, h, State } from '@stencil/core';
import { iFormField } from '../../models/form-field.model';

@Component({
  tag: 'toggle-field',
  styleUrl: 'toggle-field.css',
  shadow: false,
  scoped: false,
})
export class ToggleField {
  @Prop({ mutable: false }) field: iFormField;
  @Prop() isChecked: boolean;
  @Prop() isInvalid: boolean;
  @Prop() isHidden: boolean;
  @Prop() isDisabled: boolean;

  @State() isTouched: boolean = false;

  @Event({ bubbles: true, composed: true }) valueChanged: EventEmitter<{ key: string; value: any }>;
  @Event({ bubbles: true, composed: true }) fieldBlurred: EventEmitter<{ key: string; }>;

  private onValueChange(e: Event) {
    const value = (e.target as HTMLInputElement).checked;
    this.valueChanged.emit({ key: this.field.key, value });
  }

  private handleBlur() {
    this.isTouched = true;
    this.fieldBlurred.emit({ key: this.field.key });
  }

  render() {
    if(this.isHidden) {
      return null;
    }
    
    return (
      <section class="toggle-field-section">
        <label>
          <input
            id={`${this.field.key}-checkbox-field`}
            type="checkbox"
            disabled={this.isDisabled}
            checked={this.isChecked}
            onChange={(e) => this.onValueChange(e)}
            onBlur={() => this.handleBlur()}
            hidden />

          <span></span>
        </label>

        <div class={`${this.isChecked ? 'checked' : '' } ${this.isTouched ? 'touched' : '' } ${this.isInvalid ? 'invalid' : '' } checkbox-field-content`}>
          {this.field.label && (
            <label
              htmlFor={`${this.field.key}-checkbox-field`}
              innerHTML={this.field.label}></label>
          )}

          {this.field.placeholder && (
            <p
              
              innerHTML={this.field.placeholder}></p>
          )}

          <slot></slot>
        </div>
      </section>
      // <Host>
      //   <label class="toggle-container">
      //     <span class={`toggle ${this.isChecked ? 'checked' : ''}`}> 
      //       <input
      //         type="checkbox"
      //         hidden
      //         checked={this.isChecked}
      //         disabled={this.isDisabled}
      //         onChange={(e) => this.onValueChange(e)}
      //         onBlur={() => this.handleBlur()}
      //       />

      //       <span class="switcher"></span>
      //     </span>
      //     <div class={`content ${this.isChecked ? 'checked' : ''}`}>
      //       {this.field.label && <h6 class="heading">{this.field.label}</h6>}
      //       {this.field.placeholder && <p class="description">{this.field.placeholder}</p>}

      //       <slot></slot>
      //     </div>
      //   </label>
      // </Host>
    );
  }
}
